import React, {useEffect, useRef, useState} from 'react';
import BodyTemplate from "../../template/BodyTemplate";
import {getItems} from "../../../api/api";
import {useParams} from "react-router-dom"
import ReactHtmlParser from "react-html-parser";
import RouteTemplate from "../../template/RouteTemplate";
import './SingleNews.css'
import TableOfContent from "../../template/TableOfContent";
import NewsTemplate from "../NewsTemplate/NewsTemplate";
import {useLocation} from 'react-router-dom'
import MetaTag from "../../template/MetaTag/MetaTag";

const SingleNews = () => {
    const {news_tag} = useParams()
    const location = useLocation()
    const path = location.pathname.split('/')[1]
    const type = path === 'tin-tuc' ? 0 : path === 'ho-tro' ? 2 : 1
    console.log(type)
    const [state, setState] = useState({
        isLoading: true,
        id: 0,
        title: '',
        body: '',
        date_created: '',
        list_related: []
    })
    const ref = useRef(null)
    useEffect(() => {
        getItems(`/news/${news_tag}`, {timeout: 20000})
            .then(res => {
                setState({
                    ...state,
                    isLoading: false,
                    id: res.data.news.id,
                    title: res.data.news.title,
                    body: res.data.news.body,
                    date_created: res.data.news.date_created,
                    list_related: res.data.list_relate,
                })
            })
    }, [path, news_tag])
    return (
        <React.Fragment>
            {
                state.isLoading ? null : <MetaTag title={state.title}/>
            }
            <BodyTemplate>
                <RouteTemplate data={[
                    {
                        name: 'Trang chủ',
                        path: '/'
                    },
                    {
                        name: type === 0 ? 'Tin tức' : type === 1 ? 'Giới thiệu' : 'Hỗ trợ',
                        path: type === 0 ? '/tin-tuc' : type === 1 ? '/gioi-thieu' : '/ho-tro'
                    },
                    {
                        name: state.title
                    }
                ]
                }/>
            </BodyTemplate>
            <NewsTemplate>
                <div>
                    <h2 style={{color: 'var(--second-bg-color)'}}>{state.title}</h2>
                    <TableOfContent container_ref={ref} path={location.pathname}/>
                    <div className='ck-content' ref={ref}>
                        {ReactHtmlParser(state.body)}
                    </div>
                </div>
            </NewsTemplate>
        </React.Fragment>

    );
};

export default SingleNews;